.head1 {
  margin-top: 104px;
  margin-bottom: 104px; }
  .head1 > .content {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap; }
    .head1 > .content > * {
      margin: 0 16px; }
      .head1 > .content > *:first-child {
        margin-left: 0; }
      .head1 > .content > *:last-child {
        margin-right: 0; }
  .head1__caption {
